<template>
  <div class="ginfo">
    <div class="wugeren"></div>
    <table>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input
            disabled
            style="width:235px;border:0px solid red;"
            v-model="formzong.fullname  "
            placeholder=""
          ></el-input>
        </td>
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input disabled style="width:235px" v-model="formzong.idcard" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="formzong.insurancecard"
            placeholder=""
          ></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-select disabled v-model="formzong.sex " placeholder="">
            <el-option
              v-for="item in sex"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-date-picker
            disabled
            style="width:235px"
            v-model="formzong.birthdate  "
            type="date"
            placeholder=""
          ></el-date-picker>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="formzong.nation  " placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td></td>
        <td>
          <el-select disabled v-model="formzong.religion" placeholder="">
            <el-option
              v-for="(item,index) in Religions"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="formzong.origin" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-select disabled v-model="formzong.education" placeholder="">
            <el-option
              v-for="item in Culture"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="formzong.census" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="formzong.detailaddr" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-select disabled v-model="formzong.medicalinsurance" placeholder="">
            <el-option
              v-for="item in Medical"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="formzong.commercialinsurance"
            placeholder=""
          ></el-input>
        </td>

        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="formzong.endowmeninsurance"
            placeholder=""
          ></el-input>
        </td>
        <td></td>
        <td></td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sex: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Religions: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Culture: [
        {
          value: "",
          label: ""
        },

        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Medical: [
        {
          value: "",
          label: ""
        },

        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },

      formzong: {
        birthdate: "",
        fullname: "",
        sex: "",
        idcard: "",
        insurancecard: "",
        nation: "",
        origin: "",
        census: "",
        detailaddr: "",
        commercialinsurance: "",
        endowmeninsurance: "",
        medicalinsurance: "",
        education: "",
        religion: "",
        id: this.did
      },
      xinformz: ""
    };
  },
  props: {
    did: null
  },
  created() {
    console.log(this.did);
    if (this.did != undefined) {
      this.$ajax
        .post(process.env.API_HOST + "/person/getPersonById", `id=${this.did}`)
        .then(respones => {
          this.formzong = respones.data.prnPerson;
        });
    }
  },
  watch: {
    formzong: {
      handler(newName, oldName) {
        this.xinformz = newName;
      },
      deep: true
    },
    did(val) {
      console.log(val);
      if (val != undefined) {
        this.$ajax
          .post(
            process.env.API_HOST + "/person/getPersonById",
            `id=${this.did}`
          )
          .then(respones => {
            this.formzong = respones.data.prnPerson;
          });
      }
    }
  },
  methods: {}
};
</script>


<style>
.wugeren {
  width: 100%;
  height: 400px;
  position: absolute;
  z-index: 99;
  opacity: 0;
  background-color: red;
}
.el-input.is-disabled .el-input__inner {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #000;
  cursor: not-allowed;
}
.ginfo table {
  border: none;
}
/* .ginfo .el-input__inner{
    border:1px solid #fff;
    
} */
.ginfo {
  height: 500px;
}
.baocun {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 999;
}
</style>




