<template>
  <div id="wuzaiyuan">
    <div class="wuzai"></div>
    <table>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input disabled style="width:235px" v-model="yaopost.hospitalid" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td style="width:392px">
          <!-- <el-input disabled style="width:235px" v-model="roomif" placeholder=""></el-input> -->
          <div class="bedwei">{{roomif}}</div>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="yaopost.doctor" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="yaopost.remark" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-select disabled v-model="yaopost.isinhospital" placeholder="">
            <el-option
              v-for="item in zaiyuanstate"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td></td>
        <td>
          <el-select disabled v-model="yaopost.nursinglevel" placeholder="">
            <el-option
              v-for="item in grade"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-select disabled v-model="yaopost.health" placeholder="">
            <el-option
              v-for="item in Healthy"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="yaopost.physicalcondition"
            placeholder=""
          ></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="yaopost.carepoint" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="yaopost.diettaboo" placeholder=""></el-input>
        </td>
        <td></td>
        <td>
          <el-select disabled v-model="yaopost.bloodtype" placeholder="">
            <el-option
              v-for="item in blood"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input disabled style="width:235px" v-model="yaopost.drugreaction" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input disabled style="width:235px" v-model="yaopost.code" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-date-picker
            disabled
            style="width:235px"
            v-model="yaopost.admissiondate"
            type="date"
            placeholder=""
          ></el-date-picker>
        </td>
        <td></td>
        <td>
          <span></span><el-input disabled size="mini" style="width:69px;margin-right:10px;" v-model="yaopost.upperhypertension" placeholder=""></el-input>
          <span></span><el-input disabled size="mini" style="width:69px" v-model="yaopost.lowerhypertension" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <span></span>
          <el-input
            disabled
            size="mini"
            style="width:69px;margin-right:10px;"
            v-model="yaopost.upperhypotension"
            placeholder=""
          ></el-input>
          <span></span>
          <el-input
            disabled
            size="mini"
            style="width:69px"
            v-model="yaopost.lowerhypotension"
            placeholder=""
          ></el-input>
        </td>

        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="yaopost.uppertemperature"
            placeholder=""
          ></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="yaopost.lowertemperature"
            placeholder=""
          ></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="yaopost.upperheartrate"
            placeholder=""
          ></el-input>
        </td>

        <td style="width:100px"></td>
        <td>
          <el-input
            disabled
            style="width:235px"
            v-model="yaopost.lowerheartrate"
            placeholder=""
          ></el-input>
        </td>
        <td style="width:100px"></td>
        <td></td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      yaopost: {
        hospitalld: "",
        code: "",
        roomif: "",
        affiliateddoctor: "",
        remark: "",
        isinhospital: "",
        nursinglevel: "",
        health: "",
        physicalcondition: "",
        carepoint: "",
        diettaboo: "",
        bloodtype: "",
        drugreaction: "",
        id: this.did,
        admissiondate: "",
        upperhypertension: "",
        lowerhypertension: "",
        uppertemperature: "",
        lowertemperature: "",
        upperheartrate: "",
        lowerheartrate: ""
      },
      roomif: "",
      i: 0,
      grade: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Healthy: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      zaiyuanstate: [
        {
          value: "0",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "1",
          label: ""
        }
      ],
      blood: [
        {
          value: "A",
          label: "A"
        },
        {
          value: "B",
          label: "B"
        },
        {
          value: "O",
          label: "O"
        },
        {
          value: "AB",
          label: "AB"
        },
        {
          value: "RhA",
          label: "RhA"
        },
        {
          value: "RhB",
          label: "RhB"
        },
        {
          value: "RhO",
          label: "RhO"
        },
        {
          value: "RhAB",
          label: "RhAB"
        }
      ],
      xinyaopost: ""
    };
  },
  props: {
    did: null
  },
  created() {
    this.$ajax
      .post(process.env.API_HOST + "/bed/getNursingHomesBedTree")
      .then(res => {
        this.$ajax
          .post(
            process.env.API_HOST + "/person/getPersonById",
            `id=${this.did}`
          )
          .then(respones => {
            this.yaopost = respones.data.prnPerson;
            this.huoqu(res.data, respones.data.positionArr);
          });
      });
  },
  watch: {
    yaopost: {
      handler(newName, oldName) {
        this.xinyaopost = newName;
      },
      deep: true
    },
    did(val) {
      this.$ajax
        .post(process.env.API_HOST + "/bed/getNursingHomesBedTree")
        .then(res => {
          this.$ajax
            .post(
              process.env.API_HOST + "/person/getPersonById",
              `id=${this.did}`
            )
            .then(respones => {
              this.yaopost = respones.data.prnPerson;
              this.roomif = "";
              this.i = 0;
              this.huoqu(res.data, respones.data.positionArr);
            });
        });
    }
  },
  methods: {
    huoqu(arr, data) {
      // 
      arr.forEach(element => {
        if (element.value == data[this.i]) {
          if (this.i != 0 && this.i < 5) {
            this.roomif += "  " + element.label;
          }
          this.i++;

          if (element.children) {
            this.huoqu(element.children, data);
          }
        }
      });
    }
  }
};
</script>
<style>
.wuzai {
  width: 100%;
  height: 510px;
  position: absolute;
  z-index: 99;
  opacity: 0;
  background-color: rebeccapurple;
}
.el-input.is-disabled .el-input__inner {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #000;
  cursor: not-allowed;
}
.zhuinfo table {
  border: none;
}
.baocun {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 999;
}
.bedwei {
  width: 218px;
  height: 38px;
  line-height: 38px;
  padding-left: 15px;
  background-color: #f5f7fa;
  border: 1px solid #e4e7ed;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  position: relative;
  z-index: 1;
}

.bedwei:hover {
  /* white-space: normal; */
  overflow: visible;
}
</style>




